*{
    font-family: 'Lato', sans-serif;
    font-weight: 300;
}
#avatar{
    width: 75px;
    height: 75px;
   
}
.save{
    position: fixed;
    background-color:white;
    border: 2px solid black;
    border-radius:25px;
    width: 75px;
    color: black;
    font-weight: 500;
    text-align: center;
    padding: 5px;
    top:0;
    right:5px;
    cursor: pointer;
    
}
.action{
    border: 2px solid black;
    max-width: 300px;
    margin: 10px;
    cursor:pointer;
}
#header{
    display:grid;
    position: fixed;
    height:100px;
    width:100%;
    top:0;
    background-color: grey;
    color:white;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 100%;
}
.header-text{
    left: 100px;
}
#avatar-image{
    padding: 5px;
}
.text{
    padding-left: 5px;
    font-size: 1em;
    text-decoration: none;
}
.subject{
    
    font-weight: 500;
}
#container{
    margin-top: 100px;
    font-size: 20px;
    text-decoration: none;
    margin-left: 5px;
}

@media only screen and (min-width: 1024px){
    
    #avatar{
        position:absolute;
        flex: 2%;
        max-height: 20%;
        
    }
    #avatar-image{
        height:100px;
        
    }
    .header-text{
        position:relative;
       
        
    }
    #header{
        display:grid;
        
    }
    #container{
        display:grid;
        grid-template-columns: 50% 50%;
        grid-template-rows: 100%;
    }
    #actions{
        grid-column:1/2 ;
        grid-row: 1/2;
    }
    #stats{
        grid-row: 1/2;
        grid-column: 2/3;
    }
    .action{
        max-width: 450px;
    }
    @media only screen and (max-width: 375px){
        #header{
        
        }
        #avatar{
            position:absolute;
            flex: 20%;
            max-height:150px ;
        }
        #avatar-image{
            height: 100%;
            max-width: 100%;
           
           display: block;
           margin:auto;
            
        }
    }
}